				"html": "<h1>What is Twitch?</h1>\n<p>Twitch is a live streaming video platform that was founded in June 2011. More than 1.7 million people broadcast on Twitch each month with 100 million unique visitors. Click <a href=\"https://www.twitch.tv/p/about\">here</a> to find out more about Twitch and it's statistics.</p>\n<h2>Setting up your Twitch Account</h2>\n<p>Creating an account with Twitch enables you to interact with chat, get alerts when someone you know streams and maybe even stream yourself.</p>\n<ol>\n<li>\n<p>To create a new account for Twitch go to this <a href=\"https://passport.twitch.tv/users/new\">page</a>.</p>\n</li>\n<li>\n<p>You should now choose an appropriate username and password.</p>\n</li>\n</ol>\n<blockquote>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/e/e00617420bb5e757ea3120960c8a99f2b17c230d.gif\" alt=\"username and password\"></p>\n</blockquote>\n<ol>\n<li>Then submit your date of birth and your email address.</li>\n</ol>\n<blockquote>\n<p>It's important that you provide a correct email address as you will need this later to verify your account and get alerts when someone goes live.</p>\n</blockquote>\n<ol>\n<li>\n<p>Now all you need to do is complete the captcha and you're ready to rock!</p>\n</li>\n<li>\n<p>Once you have signed up, you will receive an email regarding verifying your account.</p>\n</li>\n</ol>\n<h3>Changing some Settings</h3>\n<p>Once you've logged in, go to the settings <a href=\"https://www.twitch.tv/settings\">page</a>.</p>\n<p>On this page you can change your profile picture, email address and write a bio. Unfortunately, Twitch doesn't allow you to change your username once you've signed up. However, you can change the capitalization.</p>\n<h2>Following FreeCodeCamp</h2>\n<h3>We are currently moving away from using Twitch, we will update this page once details have been finalized.</h3>\n<p>To follow FreeCodeCamp on Twitch, go to our channels <a href=\"https://www.twitch.tv/freecodecamp/profile\">page</a>. Press the follow button and enable alerts as show below. You will now be sent an email every time FreeCodeCamp goes live.</p>\n<blockquote>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/6/6ac2df7b5472a8c530b1feea677a315fdad2eea2.gif\" alt=\"following freecodecamp\"></p>\n</blockquote>\n<h3>Watching past broadcasts</h3>\n<p>If we aren't live, you can watch some of our previous streams. Past broadcasts are only saved for 14 days after they are originally streamed, so bare that in mind.</p>\n<blockquote>\n<p><img src=\"https://i.gyazo.com/976a213442b0a2541b90ae37f38ff043.gif\" alt=\"showing past broadcasts\"></p>\n</blockquote>",
